.footer {
    background: #303036;
    color: #d3d3d3;
    height: 400px;
}

.footer__content {
    font-size: 15px;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    font-size: 1.25rem;
}

.footer__section {
    width: 25%;
    text-align: center;
}

.footer__about__content {
    color: hsl(210, 22%, 49%);
    margin-bottom: 2rem;
}

.footer__section.links {
    line-height: 3rem;
}